           @if $s != '' { $suffix : '#{$s}-#{$i}'; }\n            @else        { $suffix : '#{$i}';       }\n\n            $order : '.flex-order-#{$suffix}';\n        }\n\n        #{$order} {\n            order: #{$i};\n        }\n    }\n}\n\n@mixin offset-for-name($sizes:null) {\n    @if $sizes == null { $sizes : ''; }\n\n    @for $i from 0 through 19 {\n        $offsets : '';\n        $suffix : '';\n\n        @each $s in $sizes {\n            @if $s != '' { $suffix : '#{$s}-#{$i * 5}'; }\n            @else        { $suffix : '#{$i * 5}';       }\n\n            $offsets :\n                '.offset-#{$suffix}, ' +\n                '.flex-offset-#{$suffix}, ' +\n                '.layout-margin .flex-offset-#{$suffix}, ' +\n                '.layout-margin .offset-#{$suffix}';\n        }\n\n        #{$offsets} {\n            @if $i != 0 { @include rtl-prop(margin-left, margin-right, #{$i * 5 + '%'}, auto); }\n            @else { @include rtl-prop(margin-left, margin-right, 0, auto); }\n        }\n    }\n\n    @each $i in 33 {\n        $offsets : '';\n        $suffix : '';\n\n        @each $s in $sizes {\n            @if $s != '' {  $suffix : '#{$s}-#{$i}';   }\n            @else        {  $suffix : '#{$i}';         }\n\n            $offsets :\n                '.offset-#{$suffix}, ' +\n                '.flex-offset-#{$suffix}, ' +\n                '.layout-margin .flex-offset-#{$suffix}, ' +\n                '.layout-margin .offset-#{$suffix}';\n        }\n\n        #{$offsets} {\n            margin-left: calc(100% / 3);\n        }\n    }\n\n    @each $i in 66 {\n        $offsets : '';\n        $suffix : '';\n\n        @each $s in $sizes {\n            @if $s != '' {  $suffix : '#{$s}-#{$i}';   }\n            @else        {  $suffix : '#{$i}';         }\n\n            $offsets :\n                '.offset-#{$suffix}, ' +\n                '.flex-offset-#{$suffix}, ' +\n                '.layout-margin .flex-offset-#{$suffix}, ' +\n                '.layout-margin .offset-#{$suffix}';\n        }\n\n        #{$offsets} {\n            @include rtl-prop(margin-left, margin-right, calc(200% / 3), auto);\n        }\n    }\n}\n\n@mixin layout-for-name($name: null) {\n    @if $name == null { $name : '';          }\n    @if $name != ''   { $name : '-#{$name}'; }\n\n    .layout#{$name},\n    .layout#{$name}-column,\n    .layout#{$name}-row {\n        box-sizing: border-box;\n        display: -webkit-box;\n        display: -webkit-flex;\n        display: -moz-box;\n        display: -ms-flexbox;\n        display: flex;\n\n        min-width: 0;\n        min-height: 0;\n    }\n\n    .layout#{$name}-column {\n        flex-direction: column;\n    }\n\n    .layout#{$name}-row {\n        flex-direction: row;\n    }\n}\n\n@mixin flex-properties-for-name($name: null) {\n    $flexName: 'flex';\n    @if $name != null {\n        $flexName: 'flex-#{$name}';\n        $name: '-#{$name}';\n    } @else {\n        $name: '';\n    }\n\n    // === flex: 1 1 0%;\n    .#{$flexName}             { flex: 1;         box-sizing: border-box; }\n\n    .#{$flexName}-grow        { flex: 1 1 100%;  box-sizing: border-box; }\n\n    .#{$flexName}-initial     { flex: 0 1 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-auto        { flex: 1 1 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-none        { flex: 0 0 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-noshrink    { flex: 1 0 auto;  box-sizing: border-box; }\n\n    .#{$flexName}-nogrow      { flex: 0 1 auto;  box-sizing: border-box; }\n\n    // (1-20) * 5 = 0-100%\n    @for $i from 0 through 20 {\n        $value : #{$i * 5 + '%'};\n\n        .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: #{$value};\n            max-height: 100%;\n            box-sizing: border-box;\n        }\n\n        .layout-row > .#{$flexName}-#{$i * 5} {\n            flex: 1 1 100%;\n            max-width: #{$value};\n            max-height: 100%;\n            box-sizing: border-box;\n\n            // 